import axios from "axios";
import styles from "./feedback.module.scss";
import { useForm } from "react-hook-form";
import React, { useState } from "react";
const Feedback = () => {
  const [stateForm, setStateForm] = useState(false);
  const checkForm = () => {
    setStateForm(!stateForm);
  };
  const {
    register,
    formState: { errors },
    handleSubmit,
    reset,
  } = useForm({
    mode: "onChange",
  });
  const onSubmit = (data) => {
    axios.post("https://64dbcbc0593f57e435b16da2.mockapi.io/feedback", data);
    console.log(JSON.stringify(data));
    reset();
    setStateForm(!stateForm);
  };
  return (
    <div>
      <h2 className={styles.feedBack_title}> </h2>
      {stateForm === false ? (
        <form
          onSubmit={handleSubmit(onSubmit)}
          className={styles.feedBack_form}
        >
          <div className={styles.inputContainer}>
            <input
              type="text"
              placeholder=""
              {...register("name", {
                required: "   ",
                pattern: {
                  value: /^[-][-]*/,
                  message: "   /  ",
                },
              })}
            />
            <div className={styles.inputCheck}>
              {errors?.name && <p>{errors?.name?.message}</p>}
            </div>
          </div>

          <div className={styles.inputContainer}>
            <input
              type="tel"
              placeholder=""
              {...register("phone", {
                required: "   ",
                pattern: {
                  value: /^((8|\+7)[\- ]?)?(\(?\d{3}\)?[\- ]?)?[\d\- ]{7,10}$/,
                  message: `    "+79261234567"`,
                },
              })}
            />
            <div className={styles.inputCheck}>
              {errors?.phone && <p>{errors?.phone?.message}</p>}
            </div>
          </div>

          <div className={styles.inputContainer}>
            <input
              type="email"
              placeholder=""
              {...register("email", {
                required: "   ",
                pattern: {
                  value:
                    /^(([^<>()[\].,;:\s@"]+(\.[^<>()[\].,;:\s@"]+)*)|(".+"))@(([^<>()[\].,;:\s@"]+\.)+[^<>()[\].,;:\s@"]{2,})$/iu,
                  message: "  Email",
                },
              })}
            />
            <div className={styles.inputCheck}>
              {errors?.email && <p>{errors?.email?.message}</p>}
            </div>
          </div>

          <textarea
            {...register("comment")}
            placeholder=""
            className={styles.textArea}
            cols="30"
            rows="10"
          ></textarea>
          <button className={styles.btn} type="submit">
            
          </button>
        </form>
      ) : (
        <div className={styles.feedback_comment}>
          <img src="img/feedback/comment_add.png" alt="CommentAdd" />
          <h2>  !</h2>
          <h2>  !</h2>
        </div>
      )}
    </div>
  );
};
export default Feedback;
